@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);


* {
  box-sizing: border-box;
}

body, h2, p {
  margin: 0;
}

main {
  margin: 5rem auto;
  width: 80vh;
}

.navbar {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  padding: 1.5rem;
  justify-content: center;
  background-color: rgb(49, 48, 48);
  text-align: center;
}

.navbar a, .huskeliste-nav {
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  color: white;
  text-decoration: none;
  letter-spacing: 0.2rem;
  font-size: 22px;
}


.huskeliste-nav {
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
  font-weight: 100;
}

.navbar a.chosen,
.navbar a:hover {
  border-bottom: 2px solid mediumaquamarine;
}
  

.overskrift {
  margin: 1rem;
}
.overskrift {
    font-size: 22px;
    text-transform: uppercase;
    font-weight: 400;
    text-align: center;
    font-family: 'Bebas Neue';
    letter-spacing: 0.1rem;
    font-size: 30px;
}

.inputChores {
    font-family: 'Roboto Condensed', sans-serif;
    letter-spacing: 0.09rem;

    width: 20rem;
    text-align: center;
    font-size: 12px;
    padding: 0.3rem;
    border-radius: 5px;
    display: flex;
    margin: 1rem auto 2rem auto;
    background-color: #eee;
}

.huskeliste-item {
    font-family: 'Roboto Condensed', sans-serif;
    letter-spacing: 0.09rem;
    list-style: none;
    display: grid;
    grid-template-columns: 4fr  0.2fr 1fr;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    line-height: 1.8rem;
}

.btnLater, .btnNow {
    padding: 0;
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 0.1rem;
    border-radius: 5px;
    width: 110px;
    margin: 0.1rem 0 0.1rem 0;
}

.btnDelete {
    padding: 0;
    width: 20px;
    font-family: 'Bebas Neue';
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 400;
    border-radius: 5px;
    text-align: center;
    margin: 0.1rem;
}
